import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { ReactiveFormsModule , FormsModule }   from '@angular/forms';
import { AppComponent } from './app.component';
import { ContatoComponent } from './contato/contato.component';
import { RouterModule } from '@angular/router';

import { HttpModule } from '@angular/http';
import { ArtigoComponent } from './artigo/artigo.component';
import { routes } from './routes';
import { HomeComponent } from './home/home.component';
import { ChatComponent } from './chat/chat.component';

@NgModule({
  declarations: [ AppComponent, ContatoComponent, ArtigoComponent, HomeComponent, ChatComponent ],
  imports: [ BrowserModule, ReactiveFormsModule, 
  HttpModule, RouterModule.forRoot(routes), FormsModule],
  providers: [ ],
  bootstrap: [AppComponent]
})
export class AppModule { }


//função vazia
var fat1 = () => {};
var long1 = function() {};
 
//potencia
var fat2 = x => x * x;
var long2 = function(x) {return x * x};

//adicionar dois números
var fat3 = (a, b) : Number => a + b;
var long3 = function(a, b) : Number {return a + b};


function reverseArray<T> (arg : T[] ) : T[]{
  return arg.reverse();
}

console.log(reverseArray(['a','b','c']));
console.log(reverseArray([1,2,3]));


